import { Component, ViewChild  } from '@angular/core';
import { ActionSheetController  ,AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-tipounidad2-list',
  templateUrl: 'tipounidad2-list.html',
})
export class Tipounidad2ListPage {
  @ViewChild('fileInput') fileInput;
  public loading: boolean = true;
  items: any=[];
  items2: any=[];
    generated = '';
 
    idafiliado:any='na';
    public hasFilter: boolean = false;
    idempresa:any;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
//private barcodeScanner:BarcodeScanner,
  constructor( public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth
   
    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
      });
    
  }


  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()     
  {

   


  }

  

  pideregistros() { 
      let self = this;
      self.loading = true;
      this.regService.getReg('todos','tipounidad2s').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
    this.pideregistros();
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'tipounidad2s').then((data) => {
             
              this.pideregistros();
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


    
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('Tipounidad2CreatePage', { record : item },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros();
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }

   OnExport4 = function ()
   {
     
     
   }
     
  OnExport3 = function ()
  {
    this.regService.abrelinkinterno('datosfijos/excel-asigna3');
    
  }
  OnExport = function ()
  {
    this.regService.abrelinkinterno('datosfijos/excel-asigna2');
    
  }

    OnExport2 = function ()
  {
   //subir file
   this.fileInput.nativeElement.click();
    
  }
   addRecord() : void
   {
    let addModal = this._MODAL .create('Tipounidad2CreatePage',{ record : null },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros();
      }  
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }
 
   conferenciaRecord(item : any) : void
   {
   
      this.navCtrl.push('Unidadacademica2ListPage', { record : item });
   }

     
  public processWebImage(event,op): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {

         //   var filett= readerEvent.target.result;
            console.log(readerEvent.target.result)
            let   options       : any	     = { 
              texto : readerEvent.target.result,
            bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:'upload',accion:' archivo subido pagos PAP '}
            };
            if(options){
              this.regService.createReg('upload',options,'tipounidad2s').then((result) => {
                this.msj.showError3('Exito','Proceso realizado con exito');
            }, (err) => {  
              this.msj.showError3('Error',err.error);
            });
            }

        
      
   };
      //reader.readAsDataURL(event.target.files[0]);
      reader.readAsText(event.target.files[0]);
    }
}
  

}



